html {
  height: 100%; }

.page {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: normal;
  min-height: 100%;
  color: #2C2E3F; }

.navigation {
  background-color: #ffffff; }

.navigation__close,
.navigation__open,
.navigation__toggle {
  display: none; }

.navigation__destinations {
  list-style: none; }

.navigation__inner {
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  justify-content: space-between;
  width: 1024px;
  padding-top: 36px;
  padding-bottom: 31px;
  position: relative; }

.navigation__destinations {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  position: absolute;
  left: 66.5%;
  transform: translateX(-50%); }

.navigation__item {
  cursor: pointer; }

.navigation__item:not(:last-child) {
  padding-right: 40px; }

.navigation__logo {
  width: 55px;
  height: 22px; }

.navigation__link-tel::before {
  content: "";
  position: absolute;
  width: 25px;
  height: 30px;
  background-image: url(../img/phone-icon.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
  right: 170px;
  top: 0px; }

.navigation__link {
  font-size: 14px;
  line-height: 17px;
  color: #2C2E3F; }

.navigation__link-tel {
  font-size: 18px;
  line-height: 32px;
  text-decoration: underline;
  position: relative; }

.navigation__link:hover,
.navigation__link:focus {
  color: #FE7865; }

.catalog {
  background-image: url(../img/background-catalog@1x.png);
  background-size: 1440px;
  background-position: 50% 120%;
  background-repeat: no-repeat; }

.catalog__image {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  padding-bottom: 200px; }

.catalog__image-panorama {
  min-height: 860px;
  width: 100%;
  padding: 0;
  max-width: 1920px;
  margin: 0 auto; }

.catalog__image-title,
.catalog__title {
  font-size: 55px;
  line-height: 80px;
  font-weight: 600;
  margin: 0; }

.catalog__image-card {
  max-width: 412px;
  position: absolute;
  top: 185px;
  top: 185px;
  left: 80%;
  transform: translateX(-80%); }

.catalog__image-title,
.catalog__image-text {
  color: #ffffff; }

.catalog__image-title {
  padding-bottom: 30px; }

.catalog__image-text {
  margin: 0;
  padding-bottom: 39px; }

.catalog__image-button {
  text-transform: uppercase;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 40px;
  padding-right: 179px;
  background-color: #FE7865;
  border: none; }

.catalog__image-button:hover,
.catalog__image-button:focus {
  background-color: #F17D4B; }

.catalog__image-buttontext {
  font-size: 12px;
  line-height: 15px;
  font-weight: 700;
  margin: 0;
  position: relative; }

.catalog__image-buttontext::after {
  content: "";
  position: absolute;
  left: 210px;
  top: 0px;
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  background-image: url(../img/arrow-down.png); }

.catalog__header {
  margin-bottom: 55px;
  padding-left: 30px; }

.catalog__title {
  max-width: 358px;
  position: relative;
  padding-bottom: 65px; }

.catalog__title::after {
  content: "";
  position: absolute;
  bottom: 20px;
  left: 10px;
  right: 10px;
  max-width: 229px;
  border: 4px solid #FE7865; }

.catalog__title-text {
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  max-width: 328px;
  letter-spacing: -0.5px;
  color: rgba(44, 46, 63, 0.3); }

.catalog__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0;
  margin: 0; }

.catalog__item {
  position: relative;
  margin-bottom: 98px; }

.catalog__nameplate {
  position: absolute;
  bottom: 8px;
  left: 37px;
  padding: 10px 20px;
  min-width: 215px;
  min-height: 61px;
  background-color: #ffffff;
  border: none; }

.nameplate__text {
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  text-align: center; }

.catalog__picture {
  position: relative; }

.catalog__button {
  background-color: inherit;
  position: absolute;
  bottom: 258px;
  left: 17px;
  border-radius: 1px;
  border-color: #ffffff;
  color: #ffffff;
  padding: 30px 25px;
  clip: rect(0 0 0 0);
  overflow: hidden;
  text-transform: uppercase; }

.catalog__item:hover,
.catalog__item:focus {
  opacity: 0.6; }

.catalog__item:hover .catalog__button,
.catalog__item:focus .catalog__button {
  clip: auto;
  overflow: visible;
  background: rgba(20, 21, 23, 0.6); }

.catalog__offers {
  position: relative; }

.offers__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 0;
  margin: 0;
  margin-bottom: 110px; }

.offers__list::after {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 0;
  right: 0;
  border: 1px solid rgba(44, 46, 63, 0.3); }

.offers__link {
  color: rgba(44, 46, 63, 0.3);
  font-size: 20px;
  line-height: 24px; }

.offers__link:hover,
.offers__link:focus {
  color: #FE7865; }

.offers__link:active {
  color: #FE7865; }

.offers__link--active {
  color: #FE7865; }

.wrapper {
  display: flex;
  justify-content: space-between;
  position: relative; }

.offers__description {
  min-width: 370px; }

.offers__title {
  font-size: 55px;
  line-height: 90px;
  font-weight: 600;
  position: relative;
  margin-top: 0;
  margin-bottom: 53px; }

.offers__title::after,
.reviews__title::after {
  content: "";
  position: absolute;
  bottom: -19px;
  left: 0;
  right: 10px; }

.offers__title::after {
  max-width: 160px;
  border: 4px solid #FE7865; }

.offers__text {
  max-width: 370px;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.5px;
  margin-top: 0;
  margin-bottom: 50px; }

.offers__button {
  background-color: #FE7865;
  padding: 30px 40px;
  text-transform: uppercase;
  border: none;
  color: #ffffff; }

.offers__reviews {
  background-color: #ffffff;
  max-width: 382px;
  padding: 30px;
  position: absolute;
  right: 0;
  top: 100px;
  z-index: 1; }

.reviews__title {
  font-size: 30px;
  line-height: 37px;
  font-weight: 50;
  letter-spacing: -0.7px;
  margin-top: 0;
  margin-bottom: 32px;
  position: relative; }

.reviews__title::after {
  max-width: 80px;
  border: 1px solid #FE7865; }

.reviews__text {
  margin-top: 0;
  margin-bottom: 35px; }

.reviews__author {
  display: flex;
  justify-content: flex-start; }

.author__name {
  margin: 0;
  padding-left: 16px; }

.reviews__image {
  position: absolute;
  right: 120px;
  top: 100px;
  z-index: 0; }

.footer__inner {
  padding-left: 10px;
  padding-right: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 80px; }

.footer__destinations-inner {
  display: flex; }

.footer__contacts {
  display: flex;
  flex-direction: column; }

.destinations__list {
  list-style: none;
  padding: 0; }

.destinations__list:not(:last-child) {
  padding-right: 30px; }

.destinations__title,
.contacts__title,
.description__title {
  font-size: 18px;
  line-height: 32px;
  font-weight: 600; }

.destinations__link,
.description__text {
  color: rgba(44, 46, 63, 0.3); }

.contacts__link {
  position: relative; }

.contacts__link-tel,
.contacts__link-mail {
  padding-left: 35px;
  text-decoration: underline;
  color: #FE7865; }

.contacts__link-tel::before,
.contacts__link-mail::before {
  content: "";
  position: absolute;
  left: -2px;
  top: 0px;
  background-repeat: no-repeat; }

.contacts__link-tel::before {
  width: 28px;
  height: 28px;
  background-image: url(../img/phone-icon-footer.svg); }

.contacts__link-mail::before {
  width: 26px;
  height: 20px;
  background-image: url(../img/mail-icon-footer.svg); }
